import { createGlobalStyle } from "styled-components";

export const GlobalStyle = createGlobalStyle`
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --pwf-change-mode: 725px;
  /* color */
  --pwf-signiture-color: rgb(49, 48, 48);
  --pwf-white: #fff;
  --pwf-gray: rgb(213, 211, 211);
  --pwf-light-gray: rgb(78, 77, 77);
  --pwf-blue: #0078ff;
  --pwf-red: #b91c1c;
  --pwf-error-color: #fef2f2;
  --pwf-notactive-blue: rgb(62, 62, 180);
  --pwf-background-transparent: rgb(0, 0, 0, 0.6);
  --pwf-background-transparent-light: rgba(0, 0, 0, 0.1);

  /* scroll */
  --scroll-size: 7px;

  // user input
  --user-input-margin-left: 8px;
  --user-input-font-size: 10.3px;
  --user-input-width: 250px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  background-color: var(--pwf-signiture-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

button {
  outline: none;
  cursor: pointer;
}

input {
  outline: none;
}

.page {
  width: 100%;
  height: 100vh;
  background-color: var(--pwf-signiture-color);
}


`;
